.loginPage {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 80px;
  padding: 50px 150px; }

.loginError {
  border: 1px solid red;
  border-radius: 2px;
  padding: 5px;
  color: red;
  font-weight: bold;
  width: max-content;
  margin: 5px; }

button {
  margin: 5px 0; }



.signUp {
  display: inline-block;
  margin-left: 120px; }

.signUp, .signIn {
  border: 3px solid rgba(black, .2);
  border-radius: 5px;
  padding: 15px;
  text-align: center;
  background-color: rgba(white, .5); }

.h3, p {
  margin: 5px 0; }

.span {
  font-size: 19px;
  margin-bottom: 40px; }
